import { observable, action, computed } from 'mobx'

export interface roomTypes {
  roomId: string
}

class RootStore {
  constructor() {
    const ws = new WebSocket('ws://localhost:8080/ws')
    ws.addEventListener('message', this.onListenMessage.bind(this))
    ws.addEventListener('close', this.socketCloseListener)
    this.conn = ws
    this.initWS = true
  }

  @observable initWS: boolean = false
  @observable onRetryInitWS: boolean = false
  @observable messages: Array<any> = []
  @observable roomMessages: Array<any> = []
  @observable roomInfo?: roomTypes = undefined
  conn: WebSocket

  @action socketCloseListener = () => {
    if (this.conn) {
      this.onRetryInitWS = true
      console.error('Disconnected.')
    }
    setTimeout(() => {
      const ws = new WebSocket('ws://localhost:8080/ws')
      ws.addEventListener('message', this.onListenMessage.bind(this))
      ws.addEventListener('close', this.socketCloseListener)
      this.conn = ws
      this.initWS = true
      this.onRetryInitWS = false
    }, 2000)
  }

  onListenMessage(evt: any) {
    try {
      const obj = JSON.parse(evt.data)
      const { type } = obj
      switch (type) {
        // join room success
        case 2: {
          this.joinRoom(obj)
          break
        }
        // leave room success
        case 4: {
          this.leaveRoom(obj)
          break
        }
        // receive message from individual room
        case 6: {
          // this.leaveRoom(obj)
          console.log('message from room: ', obj)
          this.roomMessages.push(obj)
          break
        }
        default: {
          this.messages.push(obj)
          break
        }
      }
    } catch (error) {
      console.log('failed while listening message: ', error)
    }
  }

  @action pushMessage(msg: any) {
    this.messages.push(msg)
  }

  @action joinRoom(data: any) {
    const { roomId } = data
    this.roomInfo = { roomId }
    this.pushMessage(data)
  }

  @action leaveRoom(data: any) {
    this.roomInfo = undefined
    this.pushMessage(data)
  }

  @computed get WSOnlineState() {
    if (!this.conn) {
      return false
    }

    return this.conn.readyState
  }
}

export default RootStore
