import React, { Component } from 'react'
import { Row, Col, Card, message, Badge } from 'antd'
import styles from './Home.module.scss'
import { inject, observer } from 'mobx-react'
import { SendWorldMessage, PrivateRoom } from './components'
import { toJS } from 'mobx'
import { StoresTypes } from 'src/stores'

const Home = () => (
  <Row>
    <RetryingWS />
    <Col span={16}>
      <Row>
        <Col span={4} className={styles['left-side']}>
          <OnlineUsers />
        </Col>
        <Col span={20}>
          <WorldMessages />
        </Col>
      </Row>
    </Col>
    <Col span={8}>
      <PrivateRoom />
    </Col>
  </Row>
)

export default Home

const OnlineUsers = () => (
  <div>
    <Card title='Status'>
      <WSStatus />
    </Card>
  </div>
)

const WorldMessages = inject('stores')(
  observer(({ stores }: any) => {
    const { messages } = stores

    return (
      <div className={styles['left-side']}>
        <Card title='Messages'>
          <div className={styles['chat-body']}>
            {toJS(messages).map((m: any, index: number) => (
              <li key={`mess-${index}`}>{m.message}</li>
            ))}
          </div>
          <SendWorldMessage />
        </Card>
      </div>
    )
  })
)

const RetryingWS = inject('stores')(
  observer(({ stores }) => {
    if (stores.onRetryInitWS) {
      const afterClose = () => {
        if (stores.conn.readyState === 1) {
          message.success('Connection established')
        }
      }
      message.loading('Retrying connection..', 2.5).then(afterClose, () => {})
    }

    return <></>
  })
)

@inject('stores')
@observer
class WSStatus extends Component<{ stores?: any }> {
  get stores(): StoresTypes {
    return this.props.stores
  }

  state = {
    state: this.stores.conn.readyState
  }
  timerConnState: any

  componentDidMount() {
    this.timerConnState = setInterval(() => this.tick(), 250)
  }

  componentWillUnmount() {
    clearInterval(this.timerConnState)
  }

  tick() {
    this.setState({
      state: this.stores.conn.readyState
    })
  }

  render() {
    const { state } = this.state
    const { conn } = this.stores
    let badgeState

    switch (state) {
      case conn.CONNECTING: {
        badgeState = <Badge status='processing' text='Processing' />
        break
      }
      case conn.OPEN: {
        badgeState = <Badge status='success' text='Connected' />
        break
      }
      case conn.CLOSING: {
        badgeState = <Badge status='warning' text='Closing' />
        break
      }
      case conn.CLOSED: {
        badgeState = <Badge status='error' text='Disconect' />
        break
      }
      default: {
        badgeState = <Badge status='error' text='Disconect' />
        break
      }
    }
    return <li>{badgeState}</li>
  }
}
